import { Link } from "gatsby";
import React, { FC, useState } from "react";
import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";
import { Container } from "../Container/Container";
import BurgerMenuIcon from "../Icons/BurgerMenuIcon/BurgerMenuIcon";
import DesktopLogo from "../Logo/DesktopLogo";
import MobileLogo from "../Logo/MobileLogo";
import MainNavigation from "./MainNavigation/MainNavigation";
import ServiceNavigation from "./DesktopNavigation/ServiceNavigation/ServiceNavigation";
import MobileNavigation from "./MobileNavigation/MobileNavigation";
import { InternalLinks } from "../../../utils/links";

const StyledHeader = styled.header`
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
  height: 64px;
  padding-top: 15px;
  box-sizing: border-box;
  color: ${colors.primary.black};
  background-color: ${colors.primary.white};
  @media ${device.laptop} {
    height: 80px;
    padding-top: 20px;
    position: relative;
  }
  position: sticky;
  z-index: 10;
  top: 0;
`;

const DesktopHeaderWrapper = styled.div`
  justify-content: space-between;
  display: none;
  @media ${device.laptop} {
    display: flex;
  }
`;

const MobileHeaderWrapper = styled.div`
  font-family: "Rubik";
  font-size: 0.8rem;
  font-weight: 500;
  & > div:first-child {
    justify-content: space-between;
    display: flex;
    align-items: center;
    & > div {
      width: 100%;
    };
    & > div:nth-child(2) {
      text-align: center;
    };
    & > div:nth-child(3) {
      text-align: right;
    };
  };
  @media ${device.laptop} {
    display: none;
  }
`;

const Wrapper = styled.div`
  display: flex;
  gap: 50px;
  align-items: center;
`;

const Header: FC = () => {
  const [isMobileNavigationOpened, setIsMobileNavigationOpened] = useState<boolean>(false)
  return (
    <StyledHeader>
      <Container>
        <DesktopHeaderWrapper>
          <Wrapper>
            <Link to={InternalLinks.HomePage}><DesktopLogo /></Link>
            <MainNavigation />
          </Wrapper>
          <Wrapper>
            <ServiceNavigation />
          </Wrapper>
        </DesktopHeaderWrapper>
        <MobileHeaderWrapper>
          <div>
            <div onClick={() => setIsMobileNavigationOpened(st => !st)}>
              <BurgerMenuIcon isOpen={isMobileNavigationOpened}/>
            </div>
            <div>
            <Link to={InternalLinks.HomePage}><MobileLogo /></Link>
            </div>
            <div>
            <Link to={InternalLinks.ContactUs}>Sign up</Link>
            </div>
          </div>
        </MobileHeaderWrapper>
      </Container>
      <MobileNavigation isOpen={isMobileNavigationOpened}/>
    </StyledHeader>
  );
};

export default Header;
